.advantages {
  padding-top: 109px;
  padding-bottom: 124px;
  background-color: $color-ghost-white;

  @include vp-1199 {
    padding-top: 93px;
    padding-bottom: 94px;
  }

  @include vp-767 {
    position: relative;
    padding-top: 64px;
    padding-bottom: 64px;
  }
}

.advantages__container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-left: 95px;
  padding-right: 95px;

  @include vp-1199 {
    padding-left: 45px;
    padding-right: 45px;
  }

  @include vp-767 {
    padding: 0;
  }
}

.advantages__header {
  margin: 0 0 55px 0;
  font-family: $ff-alumni;
  font-style: normal;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  text-align: center;

  @include vp-1199 {
    font-size: 42px;
    line-height: 50px;
  }

  @include vp-767 {
    padding: 0 15px;
    width: 100%;
    margin-left: 0;
    margin-bottom: 57px;
    font-size: 32px;
    line-height: 38px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 240px 1fr;
  grid-template-rows: min-content min-content;
  column-gap: 60px;
  margin: 0 auto;
  padding: 0;
  list-style: none;

  @include vp-1199 {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content 1fr min-content;
    column-gap: 30px;
    row-gap: 43px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
    row-gap: 0;
    margin: 0;
  }
}

.advantages__item {
  position: relative;
  display: flex;
  column-gap: 30px;
  min-height: 153px;

  @include vp-1199 {
    min-height: auto;
  }

  @include vp-767 {
    column-gap: 0;
    flex-direction: column;
    align-items: center;
    padding: 0 46px;
  }

  &--experience {
    margin-left: 2px;
  }

  &--experience,
  &--comfort {
    align-items: flex-end;
    margin-top: 5px;
    padding-top: 20px;

    @include vp-1199 {
      margin-top: 11px;
      align-items: center;
      padding: 0;
    }

    @include vp-767 {
      margin-top: 11px;
      align-items: center;
      padding: 0 36px;
    }
  }

  &--area,
  &--comfort {
    padding-left: 25px;

    @include vp-1199 {
      padding-left: 0;
    }

    @include vp-767 {
      padding: 0;
    }
  }

  &--comfort {
    @include vp-767 {
      margin-bottom: 37px;
    }
  }

  &--area,
  &--parking {
    padding-bottom: 20px;
    border-bottom: 2px solid $color-crimson-transparent;

    @include vp-1199 {
      padding-bottom: 0;
      border-bottom: none;
    }

    @include vp-767 {
      margin-bottom: 48px;
      padding: 0 36px;
    }
  }

  &--equipment {
    grid-column: 2 / 3;
    grid-row: 1 / -1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 4px;
    padding: 58px 24px 56px;
    background-color: $color-resolution-blue;
    color: $color-default-white;

    .advantages__numbers-text-equipment {
      text-align: center;
      font-size: 26px;
      font-weight: 700;
      line-height: 150%;
      margin-top: -14px;
      margin-bottom: 33px;

      @include vp-1199 {
        font-size: 22px;
        margin-top: -21px;
        margin-bottom: 14px;
      }

      @include vp-767 {
        margin-bottom: 8px;
      }
    }

    @include vp-1199 {
      grid-column: 1 / -1;
      grid-row: 2 / 3;
      max-width: 442px;
      margin: 0 auto;
      padding: 42px 130px 38px;
    }

    @include vp-767 {
      grid-row: 3 / 4;
      margin-top: -5px;
      max-width: 100%;
      width: 100%;
      padding: 47px 52px 42px;
      margin-bottom: 44px;
    }

    .advantages__numbers:first-of-type {
      max-width: 120px;
      margin-left: -3px;
      font-size: 74px;
      text-align: center;
      background: linear-gradient($color-default-white);
      background-clip: text;
      -webkit-text-fill-color: transparent;

      @include vp-1199 {
        max-width: 320px;
        font-size: 74px;
      }

      @include vp-767 {
        position: relative;
        max-width: 220px;
        z-index: 1;
      }
    }

    .advantages__text {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      text-align: center;
      color: $color-default-white;

      @include vp-767 {
        position: relative;
        z-index: 1;
      }
    }
  }
}

.advantages__numbers {
  display: block;
  max-width: 110px;
  margin-top: -28px;
  font-weight: 800;
  font-size: 71px;
  line-height: 150%;
  background: $crimson-to-cobalt-advantages;
  background-clip: text;
  -webkit-text-fill-color: transparent;

  @include vp-1199 {
    font-size: 58px;
  }

  @include vp-767 {
    max-width: 220px;
  }
}

.advantages__numbers-blue {
  display: block;
  max-width: 110px;
  margin-top: -28px;
  font-weight: 800;
  font-size: 71px;
  line-height: 150%;
  background: $crimson-to-cobalt-advantages-blue;
  background-clip: text;
  -webkit-text-fill-color: transparent;

  @include vp-1199 {
    font-size: 58px;
  }

  @include vp-767 {
    max-width: 220px;
  }
}

.advantages__text-equipment {
  display: block;
  margin-top: -18px;
  margin-bottom: 33px;
  font-weight: 700;
  font-size: 26px;
  line-height: 150%;

  @include vp-1199 {
    margin-top: -19px;
    margin-bottom: 14px;
    font-size: 22px;
  }

  @include vp-767 {
    position: relative;
    z-index: 1;
    margin-top: -24px;
    margin-bottom: 10px;
  }
}

.advantages__numbers-text {
  display: block;
  margin-top: -23px;
  font-size: 14px;
  line-height: 150%;
  color: $color-persian-blue;

  @include vp-1199 {
    margin-top: -18px;
  }

  @include vp-767 {
    margin-bottom: 18px;
    text-align: center;
  }
}

.advantages__text {
  margin: 0;
  font-weight: 300;
  line-height: 140%;

  @include vp-767 {
    text-align: center;
  }
}

.advantages__subheader {
  margin-top: -7px;
  margin-bottom: 6px;
  font-weight: 700;
  font-size: 22px;
  line-height: 150%;

  @include vp-1199 {
    margin-top: -13px;
    margin-bottom: 2px;
    font-size: 20px;
  }

  @include vp-767 {
    text-align: center;
  }
}
